
@import 'variables';

.Header {
    background-color: $green;
    color: $white;
    padding:10px 0;
    a {text-decoration: none;}
    .branding {
        .app-logo { 
            max-width:45px; 
            margin:0 10px;
            display: block;
            float:left;
            filter: grayscale(50%) invert(100%);
            -webkit-filter: grayscale(50%) invert(100%);
        }
        .app-title {
            letter-spacing: .1em;
            line-height: 40px;
            font-size: 30px;
            margin:0;
            text-decoration: none;
            color:$white;
            @media (max-width: 768px) {
                display:none;
            }
        }
    }
    .user-info {
        font-family: $headingFont;
        text-transform: uppercase;
        text-align: right;
        .user-icon {
            display:block;
            float:right;
            margin:3px 10px;
            width:30px;
            filter: grayscale(50%) invert(100%);
            -webkit-filter: grayscale(50%) invert(100%);
        }
        span {
            line-height: 40px;
        }
        button {
            float:right;
            margin:4px 0 4px 10px;
            border-radius: 3px;
            background-color: $lime;
            color:$white;
            font-family: $headingFont;
            border:none;
            outline:none;
            padding:5px 15px;
            box-shadow:1px 1px 2px #878787;        
        }        
        @media (max-width: 768px) {
            flex-grow: 2;
        }
        @media (max-width: 768px) {
            span { display:none;}
        }
    }
}