.cards-list {
	columns: 250px;
    column-gap: 30px; 
    margin:10px 0 30px;
}

.card-item {
    border: 2px solid #ccc;
    border-radius: 5px;
    background: #fff;
    box-shadow: 2px 2px 4px 0 #ccc;
	break-inside: avoid-column;
    margin-bottom: 30px; 
    position: relative;
    max-width: 400px;;
    overflow: hidden;
    header {
		position: absolute;
		width: 100%;
		top: 0;
		background-color: rgba(0, 0, 0, 0.4);
		padding: 15px;
		color: #ffffff;
    }
    .title {
        display: inline-block;
        font-size: 17px;
        font-weight: bold;
        max-width: 53%;
        .pet-name {font-size: 18px;}
        .pet-id {font-weight: normal;}
    }
    
    .btn {
        padding: 8px 16px;
        border-radius: 8px;
        background-color: #6c95e6;
        color: #ffffff;
        border: 3px solid #d3d3d3;
        float: right;
        font-size: 20px;
        &.small {
            font-size: calc(1.3em - 5px);
            margin-left:10px;
        }
        &:disabled {
            background-color: #808080;
        }
        &.donate-button {
            float: none;
            margin: 0 auto;
            display: block;
            border:none;
            box-shadow:1px 1px 2px #878787;
        }
    }
    
    .content {
        background-color: #ffffff;
    }
    
    .photo img {
        display: block;
        width: 100%;
    }
    
    .data {
        padding: 15px;
        ul {
            margin: 0;
            padding: 0;
            list-style-type: none;
            li { display: table; width: 100%;
                b { display: table-cell; }
                span { display: table-cell; text-align: right; }
            }
        }
    }
    
    .donate {
        background-color: #d3d3d3;
        padding: 10px;
        display: flow-root;
    }
    
    .amount {
        display: inline-block;
        margin-top: 10px;
    }
    
    footer {
        background-color: #4ca734;
        color: #ffffff;
        text-align: center;
    }
    
    .call-now {
        display: block;
        padding: 15px;
        text-decoration: none;
        color: #ffffff;
        text-transform: uppercase;
    }    
}



@media (max-width: 400px) {
	.card-item {
		font-size: 18px;
	}
}


