
@import 'variables';

.UserInfo {
    color: $white;
    padding:15px 0;
    text-align: center;
    .box {
        background-color: $lightBlue;
        padding:20px 10px; 
        border-radius: 5px;
        max-width: calc(100% - 30px);
        margin:0 auto;
        h3 {text-transform: uppercase;}
        input {
            display: inline-block;
            margin:5px;
            padding:5px 10px;
        }
        input[type="submit"] {
            border-radius: 3px;
            background-color: $lime;
            color:$white;
            font-family: $headingFont;
            border:none;
            outline:none;
            padding:5px 20px;
            box-shadow:1px 1px 2px #878787;
            display: inline-block;
        }
    }
}