@import 'variables';

* {
	box-sizing: border-box;
	&:before, &:after {
		box-sizing: border-box;
	}
}

:focus { outline: none; }

body {
	margin: 0;
	padding: 0;
	font-weight: normal;
	font-family: $bodyFont;
	font-size: 16px;
}

h1, h2, h3, h4, h5 {
	margin: 0 0 .5em 0;
	padding: 0;	
	font-family: $headingFont; 
	font-weight: bold;
	letter-spacing: .05em;
}

ol, ul { list-style: none; margin:0; padding:0; }
img { max-width: 100%; height: auto; }

input.text,
input[type="text"],
input[type="button"],
input[type="submit"],
.input-checkbox {
	-webkit-appearance: none;
	border-radius: 0;
}

.wrapper {
	width: 100%;
	max-width: 1024px;
	padding: 0 15px;
	margin:0 auto;
}
.row {
	display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}
.col {
	-ms-flex-preferred-size: 0;
    flex-basis: 0;
    -ms-flex-positive: 1;
    flex-grow: 1;
	max-width: 100%;
	position: relative;
    width: 100%;
    padding-right: 15px;
	padding-left: 15px;
	&.col-100 {
		flex-basis: auto;
	}
	@media (max-width: 768px) {
		flex-grow: 1;
	}	
}

/* alignments */
.text-right {
	text-align: right;
}
.text-center {
	text-align: center;
}