
@import 'variables';

.Donations {
    color: $white;
    text-align: center;
    .donations-list {
        background-color: $lightBlue;
        border-radius: 5px;
        max-width: calc(100% - 30px);
        margin:0 auto;
        padding:0;
        h3 {text-transform: uppercase;}
        .donation-item {
            display: inline-block;
            text-align: center;
            margin:0 20px;
            .donation-amount {
                font-size: 1.1em;
                font-weight: bold;
            }
        }
    }
}

.Welcome {
    font-size: 20px;
    strong {font-family: $headingFont; text-transform: capitalize;}
}
